import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { RestData } from './restData';
import { Observable } from 'rxjs';
import { RestaurantsDetails } from './restData';
import { map } from 'rxjs/operators';
import { DataService } from './data.service';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';
import { Review } from './restData';




const httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'user-key': 'cbd991b5d9a773b484acc58833655ff7'
    })
};


@Injectable({
    providedIn: 'root'
})

export class DemoService {
    latitude: any;
    longitude: any;
    k: RestaurantsDetails;

    api_url = "https://developers.zomato.com/api/v2.1";



    //Behaviour Subject for getRestList
    private _todos: BehaviorSubject<Array<RestaurantsDetails>> = new BehaviorSubject(null);
    public readonly todos: Observable<Array<RestaurantsDetails>> = this._todos.asObservable();


    //Behaviour Subject for getRestDetail
    private _todos1: BehaviorSubject<RestaurantsDetails> = new BehaviorSubject(null);
    public readonly todos1: Observable<RestaurantsDetails> = this._todos1.asObservable();


    //Behaviour Subject for getting reviews
    private _todosReview: BehaviorSubject<Review> = new BehaviorSubject(null);
    public readonly todosReview: Observable<Review> = this._todosReview.asObservable();




    constructor(private httpClient: HttpClient, private dataService: DataService) { }

    //Below is the API call for getting your location and displaying the restaurants
    getRestList(position: Position): Observable<Array<RestaurantsDetails>> {

        const obs = this.httpClient.get<RestData>(this.api_url + '/geocode?lat=' + position.coords.latitude + '&lon=' + position.coords.longitude, httpOptions).pipe(map(
            (response: RestData) => {
                console.log(response);
                return response.nearby_restaurants;
            }


        ));

        obs.subscribe(
            res => {
                this._todos.next(res);
                console.log(res);
            });

        return obs;

    }


    //API CALL for getting the details of the selected Restaurant
    getRestDetail(id: string | number): Observable<RestaurantsDetails> {
        const obs1 = this.httpClient.get<RestaurantsDetails>(this.api_url + '/restaurant?res_id=' + id, httpOptions).pipe(map(

            (response: RestaurantsDetails) => {

                return response;
            }
        ));

        obs1.subscribe(
            res => {
                this._todos1.next(res)
                console.log(res);
            })

        return obs1;


    }


    //This API Call is going to be about getting the reviews of the selected restaurants
    getRestReviews(id: string | number): Observable<Review> {
        const obs2 = this.httpClient.get<Review>(this.api_url + '/reviews?res_id=' + id, httpOptions).pipe(map(
            //https://developers.zomato.com/api/v2.1/reviews?res_id=59865
            (response: Review) => {

                return response;
            }
        ));

        obs2.subscribe(
            res => {
                this._todosReview.next(res)
                console.log(res);
            })

        return obs2;

    }

}