import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { OffersComponent } from './offers/offers.component';
import { ContactusComponent } from './contactus/contactus.component';
import { NotfoundComponent } from './notfound/notfound.component';
import { RestaurantsNearMeComponent } from './restaurants-near-me/restaurants-near-me.component';
import { RestaurantsDetailsComponent } from './restaurants-details/restaurants-details.component';
import { LocationComponent } from './location/location.component';
import { DataService } from './data.service';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    OffersComponent,
    ContactusComponent,
    NotfoundComponent,
    RestaurantsNearMeComponent,
    RestaurantsDetailsComponent,
    LocationComponent,
    HeaderComponent,
    FooterComponent,
  ],  
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    

    
    RouterModule.forRoot([
      { path:'home',component:HomeComponent },
      { path:'', redirectTo:'home',pathMatch:'full' },
      { path:'offers',component:OffersComponent },
      { path:'contactus',component:ContactusComponent },
      { path:'restaurant', component:RestaurantsNearMeComponent },
      { path:'restaurant/details', component:RestaurantsDetailsComponent },
      { path:'**',component:NotfoundComponent },


    ]),
  ],
  providers: [DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }
